.about {
  padding-top: 18rem;
  padding-bottom: 5rem;
  background-color: #fefefe;
}
.progress {
  border: 5px solid white;
  box-shadow: 0px 0px 2px gray;
  border-radius: 15px !important;
  margin-bottom: 1rem;
  height: 1.3rem !important;
}
.progress-bar {
  background: linear-gradient(to right, #766dff, #88f2ff) !important;
  border-radius: 15px;
}
.aboutMySelf h3 {
  font-size: 2.5rem;
  text-transform: uppercase;
  font-weight: 700;
  margin-bottom: 2rem;
}

.aboutMySelf p {
  font-size: 1rem;
  letter-spacing: 0.2px;
  color: gray;
  line-height: 30px;
  margin-bottom: 2rem;
}

svg.svg-inline--fa.fa-database.fa-w-14,
svg.svg-inline--fa.fa-book.fa-w-14,
svg.svg-inline--fa.fa-user-friends.fa-w-20 {
  font-size: 1.5rem;
  margin-bottom: 0.5rem;
  color: #766dff;
}

a.allArticle {
  border: none;
  background-image: linear-gradient(
    to right,
    #766dff 0%,
    #88f3ff 48%,
    #766dff 100%
  );
  background-size: 200% auto;
  color: white;
  font-weight: 600;
  font-size: 14px;
  border-radius: 5px;
  text-decoration: none !important;
  padding: 1rem 3rem;
  text-transform: uppercase;
  transition: all 0.3s ease;
}

a.allArticle:hover {
  background-position: right center;
  color: white !important;
}

@media screen and (max-width: 576px) {
  .col-md-6.aboutMySelf.aos-init.aos-animate {
    padding: 0 3rem;
  }
  .d-flex.justify-content-between {
    flex-direction: column;
    align-items: center;
  }

  .col-md-6.progressBar.aos-animate {
    padding: 0 3rem;
    margin-top: 3rem;
  }
}

@media screen and (max-width: 992px) {
  .d-flex.aboutBtn {
    flex-direction: column;
  }
  .totalInfo {
    margin: 1rem 0;
  }
}
